<template>
  <!-- TODO: rename this to actual fitting module name --> 
    <div class="pa-2">

      <v-btn
        class="mx-2"
        @click.stop="bellRing()"
        fab
        small
        v-bind:color=bellColor
      >
        <v-icon dark>
          mdi-bell
        </v-icon>
      </v-btn>
    </div>
</template>

<script>
  export default {
    data: () => ({
      soundFile: "flute.wav",
      bellColor: "pink"
    }),
    components: {

    },
    methods: {
      bellRing() {
        console.log("bellring activated !!!");

        this.bellColor = "gray";
        var audioFile = new Audio(this.soundFile); 
        audioFile.play();
        setTimeout(() => { this.bellColor = "pink"; }, 3000);
         
      }
    }
}
</script>