<template>
  <v-container class="ml-4">
    <v-row class="display-1">
    {{ currentTime }}
    </v-row>
    <v-row class="subtitle-1">
      <p>Järgmine: {{ nextTaskDate +" "+ nextTaskTime }}</p>
    </v-row>

    <!--
    <v-row class="subtitle-1">
      <p>lastTriggerTime: {{ lastTriggerTime }}</p>

    </v-row>
    --> 
    
  </v-container>
</template>

<script>
import { format } from 'date-fns'

export default {
  props: ['task'],
  data() {
    return {
      soundFile: "flute.wav",
      currentTime: '',
      currentTimestamp: '',
      futureTasks: [],
      nextTaskDate: '', 
      nextTaskTime: '',
      lastTriggerTime: null,
      hasRinged: false
    }
  },
  methods: {
    getDate() {
      this.currentTime = format(new Date(), 'HH:mm:ss')
      setTimeout(this.getDate, 1000);
    },
    getTimestamp(){
      this.currentTimestamp = Date.now(); 
      setTimeout(this.getTimestamp, 1000); 
    },
    memoryCleanUp() {
      var tasks = this.$store.getters.tasksFiltered; 
      var entry;

      for(entry of tasks) {
        if(entry.timeStamp < Date.now() - 1296000000){
          this.$store.dispatch('deleteTask', entry.id)
        }
      }
    setTimeout(this.memoryCleanUp, 360000);
    }, 
    getFutureTasks() {
      var tasks = this.$store.getters.tasksFiltered; 

      var entry;
      this.futureTasks = []; 

      for(entry of tasks) {
        if( entry.timeStamp > Date.now() /* && entry.timeStamp < Date.now() + 86400000 */ ) {
          this.futureTasks.push(entry);
        }
      }

      this.futureTasks.sort(function(a,b){
          return a.timeStamp - b.timeStamp; 
        })
      setTimeout(this.getFutureTasks, 12000);


    },
    getNextTaskDate(){
      if(this.futureTasks.length > 0){
        if(this.futureTasks[0].taskDate == format(new Date(), 'yyyy-MM-dd')) {
          this.nextTaskDate = 'Täna kell '; 
        } else {
          this.nextTaskDate = this.futureTasks[0].taskDate; 
        }
      }
      setTimeout(this.getNextTaskDate, 12000);
    },
    getNextTaskTime(){
      if(this.futureTasks.length > 0){
        this.nextTaskTime = this.futureTasks[0].taskTime;
      }
      setTimeout(this.getNextTaskTime, 12000);
    },
    bellRing(){
      var audioFile = new Audio(this.soundFile); 

      if(this.futureTasks.length > 0 
        && this.futureTasks[0].taskDate == format(new Date(), 'yyyy-MM-dd')) {

        if( this.lastTriggerTime != format(new Date(), 'HH:mm') ){
          // console.log("hasRinged is set to false")
          this.hasRinged = false; 
          //pass
        }
        // console.log("last trigger time " + this.lastTriggerTime ); 
        // console.log("next task time " + this.futureTasks[0].taskTime ); 
        // console.log("current minute " + format(new Date(), 'HH:mm')); 

        if(this.futureTasks[0].taskTime == format(new Date(), 'HH:mm') 
          && !this.hasRinged) {


          this.lastTriggerTime = this.futureTasks[0].taskTime; 
          
          console.log("ringed at " +  format(new Date(), 'yyyy-MM-dd HH:mm:ss')  ); 

          audioFile.play();

          this.hasRinged = true; 

          //pass
        }

      }

      setTimeout(this.bellRing, 3000);
    }

  },
  computed:{
  },
  mounted() {
    this.getDate()
    this.getTimestamp()
    this.getFutureTasks()
    this.getNextTaskDate()
    this.getNextTaskTime()
    this.bellRing()
    this.memoryCleanUp()
  }
}
</script>

