<template>

  <div>
    <div class="pa-4">
      <v-row>
        <v-date-picker
          first-day-of-week=1
          locale="ET"
          event-color="primary"
          no-title
          mode="date"
          :events="datesWithTasks"
          v-model="selectedDate"
      ></v-date-picker>

      <v-btn
        class="mx-2"
        @keydown.esc="overlay = false"
        @click.stop="overlay = !overlay; calendarButtonClicked()"
        fab
        large
        color="blue lighten-1"
      >
        <v-icon dark>
          mdi-calendar-plus
        </v-icon>
      </v-btn>

    <div class="clockPosition">
      <v-overlay
        :absolute="true"
        :value="overlay"
      >
            <v-time-picker
              v-if="showClock"
              v-model="timeInput"
              scrollable
              header-color="primary"
              class="mt-4"
              format="24hr"
            >
                  <v-spacer></v-spacer>

                  <v-btn
                    @click="cancelClicked()"
                    large
                    elevation="2"
                    text
                    color="primary"
                  >
                    Cancel
                  </v-btn>

                  <v-btn
                    @click="clockOkayClicked()"
                    large
                    elevation="2"
                    text
                    color="primary"
                    v-bind:disabled="!timeInput"
                  >
                    Ok
                  </v-btn>

            </v-time-picker>
      </v-overlay>
    </div>

      </v-row>
    </div>

    <div class="pa-4">
      <v-list
        v-if="dateTaskList.length"
        class="pt-0"
        flat
      >
          <task 
            v-for="task in dateTaskList"
            :key="task.id"
            :task="task"
          />

      </v-list>

    <no-tasks
      v-else
    />

    </div>

<!-- 
    <div class="pa-4">
      Selected date is <strong>{{ selectedDate }}</strong>
      <br>Task IDs <strong>{{ dateTaskList }}</strong>
    </div>
--> 


  </div>


</template>

<script>

export default {
    props: ['task'],
    data: () => ({
      arrayEvents: null,
      selectedDate: null,
      showClock: false,
      timeInput: null,
      overlay: false
    }),

  computed: {
    tasks: {
      get() {
        return this.$store.getters.tasksFiltered
      },
      set(value) {
        this.$store.dispatch('setTasks', value)
      }
    },
    datesWithTasks: {
      get() {
        var tasks = this.$store.getters.tasksFiltered; 
        var entry;
        var datesWithTasksSet = new Set();
        for (entry of tasks) {
          datesWithTasksSet.add(entry.taskDate);
        }
        return  Array.from(datesWithTasksSet);
      }
    },
    dateTaskList: {
      get() {
        var tasks = this.$store.getters.tasksFiltered; 
        var entry;
        var taskList = []; 
        for (entry of tasks) {
          if (entry.taskDate == this.selectedDate) {
            taskList.push(entry);
          } 
        }

        for (entry of tasks) {
	        entry.timeStamp = Date.parse(entry.taskDate + " " + entry.taskTime + ":00"); 
        }

        taskList.sort(function(a,b){
          return a.timeStamp - b.timeStamp; 
        })

        return taskList; 
      }
    }




  },

  mounted(){
    this.checkSelectedDate(); 
  },

  methods: {
    checkSelectedDate(){
      if(this.selectedDate == null){
        this.selectedDate = new Date().toJSON().slice(0,10);
      }
    },
    calendarButtonClicked() {
      this.showClock = !this.showClock;
    },
    cancelClicked() {
      this.showClock = false;
      this.timeInput = null; 
      this.overlay = false;
    },
    clockOkayClicked() {
      this.showClock = false;
      this.addNewTask(); 
      this.overlay = false;
    },
    addNewTask() {
      var taskData = {};
        taskData["title"] = 'genericTaskTitle';
        taskData["dateValue"] = this.selectedDate;
        taskData["timeValue"] = this.timeInput;
      
      this.$store.dispatch('addTask', taskData);
      this.dateInput = null; 
      this.timeInput = null; 
    }

  },

  components: {
    'task': require('@/components/Todo/Task.vue').default,
    'no-tasks': require('@/components/Todo/NoTasks.vue').default
  }

}
</script>
<!--
<style lang="sass">
  .clockPosition
    color: red !important
    position: fixed
    top: 200
    left: 200
</style>
--> 