<template>
  <div >
    <v-list-item 
      :class= "passedClass" 
      class="pb-1">

      <template v-slot:default>
        <v-list-item-content>
          <v-list-item-title>
            Kuupäev {{ task.taskDate }} kell {{ task.taskTime }}
          </v-list-item-title>
        </v-list-item-content>

        <v-btn
          class="mx-2"
          fab
          dark
          small
          color="blue lighten-2"
          @click.stop="deleteTask(task.id)"
        >

          <v-icon dark>
            mdi-delete
          </v-icon>
        </v-btn>
      </template>

    </v-list-item>

    <v-divider></v-divider>

  </div>
</template>

<script>
import { format } from 'date-fns'

export default {
  data: () => ({
    passedClass: null
  }),
  props: ['task'],

  methods: {
    deleteTask(id) {
      this.$store.dispatch('deleteTask', id)
      
    },

    checkClassType(){
     if ( this.task.timeStamp < Date.now() ) {
        this.passedClass = "grey lighten-2"
      }
      setTimeout(this.checkClassType, 1000);
    }
  }, 

  mounted(){
    this.checkClassType(); 
  },

}
</script>